* {
    padding: 0px;
    margin: 0px;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    background-color: #272822;
}
#cont {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
#main {
    padding: 20px;
    width: 300px;
    text-align: center;
    border: solid 1px #75715e;
}
h1 {
    color: #bfbfbf;
    font-weight: lighter;
    font-size: 18px;
    font-family: "Consolas", "Roboto Mono", serif;
}
input {
    color: #bfbfbf;
    outline: none;
    border: solid 1px #75715e;
    background-color: #272822;
    font-size: 13px;
    font-family: "Arial", sans-serif;
    width: 90%;
    padding: 5px;
    margin-bottom: 10px;
}

button {
    opacity: 1;
    color: #bfbfbf;
    outline: none;
    border: none;
    background-color: #75715e;
    font-weight: lighter;
    font-size: 18px;
    font-family: "Consolas", "Roboto Mono", serif;
    width: 95%;
    padding: 9px;
    margin-top: 10px;
    margin-bottom: 10px;
}

button:hover {
    background-color: #696554;
}

button:active {
    opacity: 0.5;
    background-color: #5d5a4b;
}

p {
    font-size: 14px;
    color: #bfbfbf;
    margin-top: 10px;
    font-family: "menlo", sans-serif;
}

/*------------------RESPONSIVE BRO-------------------*/

@media only screen and (max-width: 1040px) {
    #main {
        padding: 6vw;
        top: calc(50% - 18vh);
        left: 6%;
        width: calc(90% - 14vw);
        text-align: center;
        border: solid 1px #75715e;
    }
    h1 {
        font-size: 6vw;
    }
    p {
        font-size: 4vw;
        margin-top: 3vw;
    }
    input {
        font-size: 4vw;
        width: 90%;
        padding: 2vw;
        margin-bottom: 3vw;
    }
    button {
        font-size: 6vw;
        padding: 2vw;
        margin-top: 3vw;
        margin-bottom: 3vw;
    }
}
